@media screen and (max-width: 768px) {
  html,
  body {
    scroll-behavior: smooth;
  }
  .dashboardMainCon {
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .dashboardSubCon {
    max-width: 1000px;
    height: 100%;
    margin-top: 2rem;
  }
  .dashboardLeftCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .profilePhoto {
    width: 250px;
    object-fit: cover;
    border-radius: 8rem;
  }
  .socialMediaCon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    margin: 2rem 0;
    width: 100%;
  }
  .dashboardRightCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
  h1 {
    font-family: "Lato", sans-serif;
    font-weight: 900;
    font-size: 2.5rem;
  }
  h4 {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.2rem;
    margin: 0.5rem 0;
    line-height: 2rem;
  }
  .welcomeTextCon {
    padding: 0 1rem;
  }
  .welcomeTextCon h4 {
    color: #bebebe;
  }
  .socialMediaIcon a {
    border: 2px solid #9a0e2a;
    padding: 0.5rem;
    border-radius: 2rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    outline: none;
  }
  .socialMediaIcon {
    outline: none;
  }
  /* .exploreBtn {
    border: 1px solid white;
    border-radius: 1rem;
    background-color: #9a0e2a;
    color: white;
    padding: 0.5rem 1rem;;
    font-size: 1.3rem;
    font-weight: 600;
    margin-top: 4rem;
    margin-left: 1rem;
  } */
  a {
    outline: none;
  }
  .exploreBtnCon {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

