@media screen and (max-width: 768px) {
  .successCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .successCon div {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
