@media screen and (max-width: 768px) {
  .projectsMainCon {
    margin: 0;
    padding: 0;
    margin-top: 3rem;
  }
  .projectsSubCon {
    display: flex;
    flex-direction: column;
    margin: 0;
    padding: 0;
    max-width: 1000px;
    text-align: center;
  }
  .projectsSubCon h3 {
    color: #9a0e2a;
  }
  .projectsTopCon {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
  }
  .stackBtn {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    background-color: #192841;
    margin: 0.4rem 0.2rem;
    border-radius: 1.5rem;
    padding: 0.1rem 0.8rem;
    border: 0.5px solid white;
  }
  .stackBtn h5 {
    color: white;
    font-family: "Lato", sans-serif;
    line-height: 0.5rem;
    font-size: 1rem;
    margin: 0;
  }
  .stackImage {
    width: 30px;
    height: 30px;
  }
  .projectsBottomCon {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .projectSingleFrame {
    width: 90%;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 1rem 0;
    background-color: #192841;
  }
  .projectImgPart {
    width: 100%;
  }
  .proejctSingleImg {
    width: 100%;
    height: 100%;
  }

  .projectBtnPart h2 {
    margin: 0.2rem 0;
    color: white;
  }

