@media screen and (max-width: 768px) {
  .servicesMainCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 8rem;
  }
  .servicesSubCon {
    margin: 2rem 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1rem;
  }
  .servicesSubCon h6 {
    color: #bebebe;
  }

  .serviceCon {
    background-color: #192841;
    padding: 2rem;
    text-align: center;
    margin: 1rem 0;
    border-radius: 1rem;
  }
  .serviceBodyCon h6 {
    color: white;
  }
  .serviceHeaderCon h2 {
    color: #9a0e2a;
  }

  h6 {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.1rem;
    font-weight: 300;
    margin: 2rem 0;
    text-align: center;
  }
  .redText {
    color: #9a0e2a;
  }
  .bolder {
    font-weight: 600;
  }
  .lineHeightLess {
    line-height: 1rem;
    margin: 1rem 0;
  }
}

/* services desktop */

@media screen and (min-width: 769px) {
  html,
  body {
    overflow-x: hidden;
  }
  .servicesMainCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100vw;
    height: 100vh;
  }
  .servicesSubCon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    padding: 0 1rem;
    height: 100%;
  }
  .servicesSubCon h6 {
    color: #bebebe;
  }

  .serviceCon {
    background-color: #192841;
    padding: 2rem;
    text-align: center;
    margin: 1rem 0;
    border-radius: 1rem;
    width: 30%;
    margin: 0 1rem;
    height: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .serviceBodyCon h6 {
    color: white;
  }
  .serviceHeaderCon h2 {
    color: #9a0e2a;
  }

  h6 {
    font-family: "Ubuntu", sans-serif;
    font-size: 1.1rem;
    font-weight: 300;
    margin: 2rem 0;
    text-align: center;
  }
  .redText {
    color: #9a0e2a;
  }
  .bolder {
    font-weight: 600;
  }
  .lineHeightLess {
    line-height: 1rem;
    margin: 1rem 0;
  }
}

/* animations */
/* Grow */
.hvr-grow {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
}
.hvr-grow:hover,
.hvr-grow:focus,
.hvr-grow:active {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
