@media screen and (max-width: 768px) {
  html,
  body {
    margin: 0;
    padding: 0;
    overflow-x: hidden;
    width: 100%;
    height: 100%;
  }
  a {
    text-decoration: none;
    color: white;
  }
  h3 {
    font-family: "Ubuntu", sans-serif;
  }
  h2 {
    font-family: "Lato", sans-serif;
  }
  .navbarMainCon {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
  }
  .navLeftCon {
    display: none;
  }
  .navRightCon {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
  }

  .hiddenNavbarMainCon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 1.5rem;
    border-bottom: 1px solid white;
  }
  .hiddenNavLeftCon {
  }
  .hiddenNavRightCon {
  }
  .closeBtn {
    border: none;
    background-color: transparent;
  }
  .showBtn {
    border: none;
    background-color: transparent;
  }
  .btns-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .change-lang-btn {
    border: none;
    background-color: transparent;
  }
  .active-lang-btn {
    background-color: white;
    border-radius: 50%;
  }
}

/* media for tablets */

@media screen and (min-width: 768px) {
  a {
    text-decoration: none;
    color: white;
  }
  h3 {
    font-family: "Lato", sans-serif;
  }
  h2 {
    font-family: "Lato", sans-serif;
  }
  .tabletNavbarMainCon {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-left: 2rem;
    border-bottom: 1px solid white;
  }
  .tabletnavLeftCon {
    flex: 1;
  }
  .tabletnavRightCon {
    flex: 1;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
  }
  /* .tabletnavRightCon div {
    margin: 0 1rem;
  } */
  .btns-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
  }
  .change-lang-btn {
    border: none;
    background-color: transparent;
  }
  .active-lang-btn {
    background-color: white;
    border-radius: 50%;
  }
}

/* animations */

/* Sink */
.hvr-sink {
  display: inline-block;
  vertical-align: middle;
  -webkit-transform: perspective(1px) translateZ(0);
  transform: perspective(1px) translateZ(0);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0);
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
  -webkit-transition-property: transform;
  transition-property: transform;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;
}
.hvr-sink:hover,
.hvr-sink:focus,
.hvr-sink:active {
  -webkit-transform: translateY(8px);
  transform: translateY(8px);
}
