@media screen and (max-width: 768px) {
  html,
  body {
    overflow-x: hidden;
  }
  .projectMainCon {
    margin: 0;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 100%;
  }
  .projectSubCon {
    margin: 0;
    padding: 0;
    max-width: 1000px;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 100%;
    height: 100%;
    padding: 0 1rem;
  }
  .projectImgDiv img {
    width: 90%;
    border-radius: 1rem;
  }
  .projectActionBtns {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
  }
  .projectActionBtns a {
    flex: 1;
    margin: 1rem 2rem;
    outline: none;
  }
  .projectActionBtns a button {
    background-color: #9a0e2a;
    color: white;
    padding: 0.7rem 2rem;
    font-size: 1.4rem;
    font-weight: 700;
    font-family: "Lato", sans-serif;
    border-radius: 2rem;
    outline: none;
    border: 1px solid white;
  }
  .projectsStackCon {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-evenly;
    flex-wrap: wrap;
  }
  .projectStackCon {
    margin: 1rem 0;
    background-color: #192841;
    border-radius: 1rem;
  }
}

